import React from 'react'
import mixpanel from 'mixpanel-browser'

import {
  useBreakpointValue,
  Box,
  Button,
  Center,
  Checkbox,
  Flex,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Spinner,
  Stack,
  Text,
  Tooltip,
  useClipboard
} from '@chakra-ui/react'

import { CopyIcon } from '@chakra-ui/icons'

import { useAnchorWallet } from '@solana/wallet-adapter-react'

import { DepositWithdrawButton } from './ApplicationCommon'
import { deposit } from './ContractDeposit'
import { NETWORK, connection } from './ContractCommon'

import otterLogoGlyph from './icons/otterlogo_glyph.svg'

interface DepositModalStageZeroContentProps {
  setStateCallback: () => void
  stageZeroWarningChecked: boolean
  stageZeroUserBalance: number
  depositCallback: () => void
  fireConfetti: () => void
  onClose: () => void
  toast: () => void
}
function DepositModalStageZeroContent (props: DepositModalStageZeroContentProps) {
  const wallet = useAnchorWallet()
  if (!wallet) {
    throw new Error('Wallet is null.')
  }
  connection.getBalance(wallet.publicKey).then(balance => {
    if (props.stageZeroUserBalance === null) {
      props.setStateCallback({ stageZeroUserBalance: balance })
    }
  })
  // If true, we warn the user that their wallet has insufficient balance.
  const warnInsufficientBalance = props.stageZeroUserBalance !== null &&
    props.stageZeroUserBalance < 1000615000
  // If true, warn the user that depositing on mobile is disallowed.  (There
  // are problems with losing secret notes).
  const warnIsMobile = useBreakpointValue({ base: true, sm: false })
  // If true, the deposit button is grayed out.
  const disallowDeposit = warnInsufficientBalance || warnIsMobile || !props.stageZeroWarningChecked
  return (
    <>
      <ModalHeader>Deposit 1 SOL</ModalHeader>
      <ModalCloseButton />
      <ModalBody>
        <Text>
          Depositing requires 3 separate approvals.
          If a transaction fails to reach the Solana network,
          the transaction will be retried,
          so 4 or 5 approvals may be required.
        </Text>
        <Center mt="5%">
          <img src={otterLogoGlyph} />
        </Center>
        <Text mt="5%">
          After the deposit transactions are sent,
          you will be given a secret note.
          If you lose this note or share it with anyone,
          your funds may be lost.
        </Text>
        <Flex
          mt="5%"
          onClick={(e) => {
            // Clicking on the actual checkbox double-sends an onClick event,
            // so we ignore one of them.
            if (e.target.tagName === 'INPUT') {
              return
            }
            props.setStateCallback({
              stageZeroWarningChecked: !props.stageZeroWarningChecked
            })
          }}
          sx={{ cursor: 'pointer' }}
        >
          <Checkbox
            isChecked={props.stageZeroWarningChecked}
            mr="3%"
            size="lg"
            colorScheme="blackAlpha"
            borderColor="bistre"
          />
          <Text
            w="90%"
            fontWeight="700"
            fontSize="0.8em"
          >
            I understand that if I lose my secret note or share it with anyone else,
            I will not be able to withdraw my funds.
          </Text>
        </Flex>
      </ModalBody>
      <Button
        variant="fancy"
        p="1em"
        opacity={disallowDeposit ? '40%' : null}
        m="5% 10% 5% 10%"
        fontFamily="Montserrat"
        border="1px"
        onClick={async () => {
          if (disallowDeposit) {
            return
          }
          mixpanel.track('Deposit Init')
          props.setStateCallback({ stage: 1, stageOneLoadingText: 'Signing transaction 1 of 3' })
          let note
          try {
            note = await deposit(
              props.depositCallback,
              props.toast
            )
          } catch (e) {
            console.error(e)
            props.onClose()
            return
          }
          // Download the note as a file.
          {
            const element = document.createElement('a')
            const file = new Blob([note + '\n'], {
              type: 'text/plain'
            })
            element.href = URL.createObjectURL(file)
            let fileName = `otter-${note.split('-')[1].slice(0, 10)}`
            if (NETWORK === 'devnet') {
              fileName += '-dev'
            }
            element.download = fileName + '.txt'
            document.body.appendChild(element)
            element.click()
          }
          props.setStateCallback({ stage: 2, stageTwoNote: note })
          props.fireConfetti()
          mixpanel.track('Deposit Success')
        }}
      >
        {warnIsMobile
          ? 'Deposits on Mobile not Allowed'
          : (warnInsufficientBalance ? 'Need 1.000615 SOL to Deposit' : 'Send Deposit Transactions')
        }
      </Button>
    </>
  )
}

interface DepositModalStageTwoContentProps {
  note: string
  onClose: () => void
}
function DepositModalStageTwoContent (props: DepositModalStageTwoContentProps) {
  const { hasCopied, onCopy } = useClipboard(props.note)
  const [warningChecked, setWarningChecked] = React.useState(false)
  return (
    <>
      <ModalHeader>Save Your Note</ModalHeader>
      <ModalBody>
        <Text>
          Your deposit has been finalized!
        </Text>
        <Text
          mt="5%"
          fontSize="1.2em"
          textAlign="left"
        >
          Secret Note
        </Text>
        <Flex
          justifyContent="right"
          mt="1%"
          border="1px"
          borderRadius="5px"
          borderColor="bistre"
          p="3%"
          focusBorderColor="bistre"
          onClick={() => {
            onCopy()
          }}
        >
          <Text
            fontSize="0.75em"
            fontFamily="Courier"
            opacity="70%"
            sx={{
              'word-break': 'break-all'
            }}
            align="left"
          >
            {props.note}
          </Text>
          <Tooltip
            label="Copied!"
            placement="top"
            isOpen={hasCopied}
            hasArrow={true}
            gutter="12"
          >
            <CopyIcon boxSize="17px" />
          </Tooltip>
        </Flex>
        <Flex
          mt="5%"
          onClick={(e) => {
            // Clicking on the actual checkbox double-sends an onClick event,
            // so we ignore one of them.
            if (e.target.tagName === 'INPUT') {
              return
            }
            setWarningChecked(!warningChecked)
          }}
          sx={{ cursor: 'pointer' }}
        >
          <Checkbox
            isChecked={warningChecked}
            mr="3%"
            size="lg"
            colorScheme="blackAlpha"
            borderColor="bistre"
          />
          <Text
            fontWeight="700"
            fontSize="0.8em"
          >
            I have saved my secret note in a safe place.
          </Text>
        </Flex>
        <Button
          w="80%"
          m="5% 10% 3% 10%"
          variant="plain"
          opacity={warningChecked ? null : '40%'}
          border="1px"
          onClick={() => {
            if (!warningChecked) {
              return
            }
            props.onClose()
          }}
        >
          Close
        </Button>
      </ModalBody>
    </>
  )
}

interface DepositModalProps {
  isOpen: boolean
  onOpen: () => void
  onClose: () => void
  fireConfetti: () => void
  toast: () => void
}

export class DepositModal extends React.Component<DepositModalProps> {
  constructor (props: DepositModalProps) {
    super(props)
    this.state = {
      stage: 0,
      stageZeroWarningChecked: false,
      stageZeroUserBalance: null,
      stageOneLoadingText: '',
      stageOneLoadingSubtext: '',
      stageTwoNote: null
    }
    this.resetState = this.resetState.bind(this)
    this.setStateCallback = this.setStateCallback.bind(this)
    this.depositCallback = this.depositCallback.bind(this)
  }

  resetState () {
    this.setState({
      stage: 0,
      stageZeroWarningChecked: false,
      stageZeroUserBalance: null,
      stageOneLoadingText: '',
      stageOneLoadingSubtext: '',
      stageTwoNote: null
    })
  }

  setStateCallback (newState) {
    this.setState(newState)
  }

  depositCallback (loadingText, loadingSubtext) {
    this.setState({
      stageOneLoadingText: loadingText,
      stageOneLoadingSubtext: loadingSubtext === undefined ? '' : loadingSubtext
    })
  }

  stageOneContent () {
    const subtext = (
      <Text mt="1%">
        {this.state.stageOneLoadingSubtext}
      </Text>
    )
    return (
      <>
        <ModalHeader>Depositing 1 SOL</ModalHeader>
        <ModalBody>
          <Spinner
            color="bistre"
            size="xl"
          />
          <Text
            mt="5%"
          >
            {this.state.stageOneLoadingText}
          </Text>
          {this.state.stageOneLoadingSubtext === '' ? null : subtext}
        </ModalBody>
      </>
    )
  }

  render () {
    return (
      <Modal
        isOpen={this.props.isOpen}
        onClose={() => {
          this.resetState()
          this.props.onClose()
        }}
        closeOnOverlayClick={this.state.stage === 0}
        closeOnEsc={this.state.stage === 0}
        isCentered
        size={this.state.stage === 2 ? 'xl' : 'lg'}
      >
        <ModalOverlay />
        <ModalContent
          bg="champagne"
          p="1%"
          borderRadius="10px"
        >
          {
            {
              0: (
                <DepositModalStageZeroContent
                  setStateCallback={this.setStateCallback}
                  stageZeroWarningChecked={this.state.stageZeroWarningChecked}
                  stageZeroUserBalance={this.state.stageZeroUserBalance}
                  depositCallback={this.depositCallback}
                  fireConfetti={this.props.fireConfetti}
                  onClose={() => {
                    setTimeout(this.resetState, 1000)
                    this.props.onClose()
                  }}
                  toast={this.props.toast}
                />
              ),
              1: this.stageOneContent(),
              2: (
                <DepositModalStageTwoContent
                  note={this.state.stageTwoNote}
                  onClose={() => {
                    setTimeout(this.resetState, 1000)
                    this.props.onClose()
                  }}
                />
              )
            }[this.state.stage]
          }
        </ModalContent>
      </Modal>
    )
  }
}

interface DepositRadioButtonProps {
  callback: () => void
  isSelected: boolean
  text: string
}
function DepositRadioButton (props: DepositRadioButtonProps): React.ReactElement {
  return (
    <Stack
      direction="column"
      w={['15%', '18%']}
      sx={{ cursor: 'pointer' }}
      onClick={props.callback}
    >
      <Center
        w={['4vw', 'min(25px, 1.5vw)']}
        h={['4vw', 'min(25px, 1.5vw)']}
        borderRadius={['2vw', 'min(12.5px, 0.75vw)']}
        border={['1px', '2px']}
        borderColor="bistre"
        bg="champagne"
      >
        <Box
          w={['2.4vw', 'min(15px, 0.9vw)']}
          h={['2.4vw', 'min(15px, 0.9vw)']}
          borderRadius={['1.2vw', 'min(7.5px, 0.45vw)']}
          bg="bistre"
          display={props.isSelected ? 'inherit' : 'none'}
        />
      </Center>
      <Text fontSize={['0.5em', '0.8em']}>
        {props.text}
      </Text>
    </Stack>
  )
}

interface DepositProps {
  onOpenModal: () => void
}
