import React from 'react'
import mixpanel from 'mixpanel-browser'
import { useNavigate } from 'react-router-dom'
import ReactCanvasConfetti from 'react-canvas-confetti'
import { bigInt } from 'snarkjs'
import { babyJub, pedersenHash } from 'circomlib'
import { web3 } from '@project-serum/anchor'

import {
  Alert,
  AlertIcon,
  Box,
  Flex,
  Spacer,
  useDisclosure,
  useToast
} from '@chakra-ui/react'

import {
  HeaderFooterWrapper,
  updateIntercomDisplay
} from './ApplicationCommon'

import { Deposit, DepositModal } from './ApplicationDeposit'
import { Withdraw, WithdrawModal, RELAYER } from './ApplicationWithdraw'
import { withdraw } from './ContractWithdraw'
import { connection, OTTER_PROGRAM_ID } from './ContractCommon'

import tabFilled from './icons/tab_filled.svg'
import tabEmpty from './icons/tab_empty.svg'

class WarningBar extends React.Component {
  constructor () {
    super()
    this.state = {
      isManuallyPaused: false,
      currentTps: null
    }
  }

  componentDidMount () {
    // fetch('https://proxy.otter.cash/isManuallyPaused')
    //   .then(resp => resp.json())
    //   .then(resp => this.setState({ isManuallyPaused: resp.isPaused }))
    const connection = new web3.Connection(web3.clusterApiUrl('mainnet-beta'))
    const refreshCurrentTps = () => {
      connection.getRecentPerformanceSamples(10).then(resp => {
        const sortedNumTxs = resp
          .map(sample => sample.numTransactions / sample.samplePeriodSecs)
          .sort((a, b) => a - b)
        const currentTps = sortedNumTxs[Math.floor(sortedNumTxs.length / 2) - 1]
        // Round to the tens place to make the number easy to read.
        this.setState({ currentTps: 10 * Math.round(currentTps / 10) })
      })
    }
    refreshCurrentTps()
    setInterval(refreshCurrentTps, 10000)
  }

  render () {
    let warningText = ''
    if (this.state.isManuallyPaused) {
      warningText = 'Deposits and withdraws are temporarily paused for a protocol upgrade.'
    } else if (this.state.currentTps && this.state.currentTps < 1900) {
      warningText = 'Solana is experiencing degraded performance. ' +
        'Transactions may be slow to confirm. ' +
        `TPS: ${this.state.currentTps}.`
    }
    return (
      warningText &&
      <Alert
        status="warning"
        bg="orange"
        fontSize={['0.8em', '0.9em']}
        fontWeight="600"
      >
        <AlertIcon color="bistre" />
        {warningText}
      </Alert>
    )
  }
}

// A wrapper that gives the withdraw content and withdraw modal access to the same state.
interface ModalAndContentStateWrapperProps {
  content: Deposit | Withdraw
  modal: DepositModal | WithdrawModal
  navigate: () => void
  toast: () => void
  onClose: () => void
  isDeposit: boolean
}
class ModalAndContentStateWrapper extends React.Component<ModalAndContentStateWrapperProps> {
  constructor (props: ModalAndContentStateWrapperProps) {
    super(props)
    this.state = {
      withdrawRelayerFee: null,
      withdrawRelayerAddress: '',
      withdrawRelayerUrlBase: '',
      withdrawInputNote: '',
      withdrawInputNoteIsSpent: false,
      withdrawInputNoteNeverDeposted: false,
      withdrawInputRecipient: '',
      withdrawModalStage: 0,
      withdrawModalStageZeroLoadingText: ''
    }
    this.getConfettiInstance = this.getConfettiInstance.bind(this)
    this.fireConfetti = this.fireConfetti.bind(this)
    this.updateState = this.updateState.bind(this)
    this.withdrawResetState = this.withdrawResetState.bind(this)
    this.withdrawCallback = this.withdrawCallback.bind(this)
    this.performWithdraw = this.performWithdraw.bind(this)
  }

  async componentDidUpdate (previousProps, previousState) {
    if (this.state.withdrawInputNote === previousState.withdrawInputNote) {
      return
    }
    // If the note has a syntax error, that will be caught in the render. No
    // need to check for `isSpent` or `neverDeposited`.
    if (
      this.state.withdrawInputNote.length === 0 ||
      this.state.withdrawInputNote.split('-').length !== 5 ||
      this.state.withdrawInputNote.includes(' ')
    ) {
      this.setState({
        withdrawInputNoteIsSpent: false,
        withdrawInputNoteNeverDeposited: false
      })
      return
    }

    // Check if the note has been spent.
    const parsedPreimage = this.state.withdrawInputNote.split('-')[2]
    const nullifierBuff = Buffer.from(parsedPreimage.slice(0, 62), 'hex')
    const nullifier = bigInt.leBuff2int(nullifierBuff)
    const nullifierHash = babyJub.unpackPoint(
      pedersenHash.hash(nullifier.leInt2Buff(31))
    )[0]
    const nullifierHashBytes = nullifierHash.beInt2Buff(32)
    const nullifierHashPDA = (await web3.PublicKey.findProgramAddress(
      [nullifierHashBytes],
      OTTER_PROGRAM_ID
    ))[0]
    const nullifierHashPDAData = await connection.getAccountInfo(nullifierHashPDA)
    const noteIsSpent = nullifierHashPDAData !== null && nullifierHashPDAData.data[8] !== 0

    // Check if the note was deposited.
    const parsedDepositFinalizeSignature = this.state.withdrawInputNote.split('-')[1]
    let txResponse = null
    for (let i = 0; i < 3; i++) {
      txResponse = await connection.getTransaction(
        parsedDepositFinalizeSignature,
        { commitment: 'confirmed' }
      )
      if (txResponse) {
        break
      }
    }
    const noteNeverDeposited = txResponse === null

    // Update the state.
    this.setState({
      withdrawInputNoteIsSpent: noteIsSpent,
      withdrawInputNoteNeverDeposited: noteNeverDeposited
    })
  }

  getConfettiInstance (instance) {
    this.confetti = instance
  }

  fireConfetti () {
    this.confetti({
      particleCount: 100,
      spread: 180,
      startVelocity: 65,
      ticks: 300
    })
  }

  updateState (newState) {
    this.setState(newState)
  }

  withdrawResetState () {
    this.setState({
      withdrawInputNote: '',
      withdrawInputNoteIsSpent: false,
      withdrawInputNoteNeverDeposited: false,
      withdrawInputRecipient: '',
      withdrawModalStage: 0,
      withdrawModalStageZeroLoadingText: ''
    })
  }

  withdrawCallback (loadingText) {
    this.setState({ withdrawModalStageZeroLoadingText: loadingText })
  }

  /**
   * This function is called on a valid withdraw button press, and manages the
   * process of generating a proof, sending to relayer, querying the status,
   * and advancing the withdraw stages.
   *
   * Note that it is a bit awkward to have this function here, as it should
   * really be in ApplicationWithdraw.tsx. However, due the fact that the
   * Withdraw and WithdrawModal components need to share state, it has to go
   * here.
   */
  async performWithdraw () {
    mixpanel.track('Withdraw Init')
    try {
      await withdraw(
        this.state.withdrawInputNote,
        this.state.withdrawInputRecipient,
        this.state.withdrawRelayerAddress,
        this.state.withdrawRelayerFee,
        this.state.withdrawRelayerUrlBase,
        this.withdrawCallback,
        this.props.toast
      )
    } catch (e) {
      console.error(e)
      this.props.onClose()
      return
    }
    this.setState({ withdrawModalStage: 1 })
    this.fireConfetti()
    mixpanel.track('Withdraw Success')
  }

  render () {
    // New props that will be added to both the Modal and Content.
    const propsToAdd = {
      fireConfetti: this.fireConfetti,
      withdrawSetState: this.updateState,
      withdrawResetState: this.withdrawResetState,
      performWithdraw: this.performWithdraw,
      toast: this.props.toast,
      ...this.state
    }
    return (
      <>
        <WarningBar />
        <HeaderFooterWrapper currentPage={this.props.isDeposit ? 'deposit' : 'withdraw'}>
          <Box w={['80%', 'min(38%, 700px)']} mt="8%">
            <Flex>
              <Box
                w={['48%', '40%']}
                bgSize="100% 100%"
                bgImage={this.props.isDeposit ? tabFilled : tabEmpty}
              >
                <Box
                  w="80%"
                  p="4%"
                  fontWeight={this.props.isDeposit ? '700' : '400'}
                  fontSize={['0.9em', '1.2em']}
                  onClick={() => {
                    mixpanel.track('Deposit Tab - Button Click')
                    this.props.navigate('/deposit', { replace: true })
                  }}
                  sx={{
                    cursor: 'pointer'
                  }}
                >
                  Deposit
                </Box>
              </Box>
              <Spacer />
              <Box
                w={['48%', '40%']}
                bgSize="100% 100%"
                bgImage={this.props.isDeposit ? tabEmpty : tabFilled}
                sx={{ transform: 'scaleX(-1)' }}
              >
                <Box
                  w="80%"
                  p="4%"
                  fontWeight={this.props.isDeposit ? '400' : '700'}
                  fontSize={['0.9em', '1.2em']}
                  onClick={() => {
                    mixpanel.track('Withdraw Tab - Button Click')
                    this.props.navigate('/withdraw', { replace: true })
                  }}
                  sx={{
                    cursor: 'pointer',
                    transform: 'scaleX(-1)'
                  }}
                >
                  Withdraw
                </Box>
              </Box>
            </Flex>
            <Box
              border={['1px', '2px']}
              borderRadius={this.props.isDeposit ? '0 0 15px 15px' : '0 0 0 15px'}
            >
              {React.cloneElement(this.props.content, propsToAdd)}
            </Box>
            <Flex>
              <Spacer />
              <Box
                w={['55%', '40%']}
                bgSize="100% 100%"
                bgImage={tabEmpty}
                sx={{ transform: 'rotate(180deg)' }}
                display={this.props.isDeposit ? 'none' : 'inherit'}
              >
                <Box
                  w="80%"
                  mr="auto"
                  p="5%"
                  fontFamily="Courier"
                  fontSize={['0.7em', '0.8em']}
                  sx={{ transform: 'rotate(180deg)', cursor: 'pointer' }}
                >
                  {RELAYER + '.sol'}
                </Box>
              </Box>
            </Flex>
          </Box>
        </HeaderFooterWrapper>
        <ReactCanvasConfetti
          style={{
            top: 0,
            left: 0,
            width: '100vw',
            height: '100vh',
            position: 'fixed',
            pointerEvents: 'none'
          }}
          refConfetti={this.getConfettiInstance}
        />
        {React.cloneElement(this.props.modal, propsToAdd)}
      </>
    )
  }
}

interface ApplicationProps {
  isDeposit: boolean
}
export default function Application (props: ApplicationProps): React.ReactElement {
  updateIntercomDisplay()
  const {
    isOpen: isOpenDepositModal,
    onOpen: onOpenDepositModal,
    onClose: onCloseDepositModal
  } = useDisclosure()
  const {
    isOpen: isOpenWithdrawModal,
    onOpen: onOpenWithdrawModal,
    onClose: onCloseWithdrawModal
  } = useDisclosure()
  const navigate = useNavigate()
  const toast = useToast()
  if (props.isDeposit) {
    return (
      <ModalAndContentStateWrapper
        content={<Deposit onOpenModal={onOpenDepositModal} />}
        modal={
          <DepositModal
            isOpen={isOpenDepositModal}
            onOpen={onOpenDepositModal}
            onClose={onCloseDepositModal}
          />
        }
        navigate={navigate}
        toast={toast}
        onClose={onCloseDepositModal}
        isDeposit={true}
      />
    )
  } else {
    return (
      <ModalAndContentStateWrapper
        content={<Withdraw onOpenModal={onOpenWithdrawModal} />}
        modal={
          <WithdrawModal
            isOpen={isOpenWithdrawModal}
            onOpen={onOpenWithdrawModal}
            onClose={onCloseWithdrawModal}
          />
        }
        navigate={navigate}
        toast={toast}
        onClose={onCloseWithdrawModal}
        isDeposit={false}
      />
    )
  }
}
