async function generateProof (
  note: string,
  recipient: web3.PublicKey,
  relayer: web3.PublicKey,
  fee: number,
  callback: () => void
) {
  callback('Fetching transaction history')

  // Parse the note and assert that everything is OK.
  const [
    parsedHeader,
    parsedDepositFinalizeSignature,
    parsedPreimage,
    parsedDepositFilledSubtrees,
    parsedProgramId
  ] = note.split('-')
  if (parsedHeader !== 'otter') {
    throw Error("Invalid note: Should start with 'otter'.")
  }
  if (parsedPreimage.length !== 124) {
    throw Error('Invalid note: Preimage is not the correct length.')
  }
  if (parsedProgramId !== OTTER_PROGRAM_ID.toString()) {
    throw Error('Invalid note: Incorrect programId.')
  }

  // Extract the nullifier and secret, and compute the nullifierHash.
  const nullifierBuff = Buffer.from(parsedPreimage.slice(0, 62), 'hex')
  const secretBuff = Buffer.from(parsedPreimage.slice(62, 124), 'hex')
  const nullifier = bigInt.leBuff2int(nullifierBuff)
  const secret = bigInt.leBuff2int(secretBuff)
  const nullifierHash = babyJub.unpackPoint(
    pedersenHash.hash(nullifier.leInt2Buff(31))
  )[0]

  // Parse the depositFilledSubtreesBuff
  const depositFilledSubtreesBuff = Buffer.from(parsedDepositFilledSubtrees, 'hex')
  const depositFilledSubtrees = []
  for (let i = 0; i < LEVELS; i++) {
    depositFilledSubtrees.push(
      bigInt.beBuff2int(depositFilledSubtreesBuff.slice(32 * i, 32 * (i + 1)))
    )
  }

  // Try to retrieve allDeposits from the otter-statistics service, and compute
  // allPreviousDeposits. If the service is offline, then manually fetch from
  // the RPC. This gives additional performance when the otter-statistics cache
  // layer is available, without sacrificing decentralization.
  let allPreviousDeposits = {}
  try {
    if (NETWORK === 'devnet') {
      throw Error('The otter-statistics service only performs a mainnet cache.')
    }
    const allDepositsResp = await fetch('https://statistics.otter.cash/allDeposits')
    if (!allDepositsResp.ok) {
      throw Error('Response is not OK.')
    }
    allPreviousDeposits = await allDepositsResp.json()
    for (let i = 0; i < Math.max(...Object.keys(allPreviousDeposits)); i++) {
      if (allPreviousDeposits[i].signature === parsedDepositFinalizeSignature) {
        break
      }
      delete allPreviousDeposits[i]
    }
  } catch (e) {
    console.log(
      'Retrieving /allDeposits from otter-statistics failed. ' +
      'Manually retrieving from the RPC instead.'
    )
    const allTxSignatures = await connection.getSignaturesForAddress(
      OTTER_MERKLE_STATE_ID,
      { until: parsedDepositFinalizeSignature },
      'confirmed'
    )
    if (allTxSignatures.length === 1000) {
      while (true) {
        const newTxSignatures = await connection.getSignaturesForAddress(
          OTTER_MERKLE_STATE_ID,
          {
            before: allTxSignatures[allTxSignatures.length - 1].signature,
            until: parsedDepositFinalizeSignature
          },
          'confirmed'
        )
        allTxSignatures.push(...newTxSignatures.slice(1))
        if (newTxSignatures.length < 1000) {
          break
        }
      }
    }
    allTxSignatures.push({ signature: parsedDepositFinalizeSignature })

    // Split into chunks of 500 so that we're not doing more than 500
    // connection.getTransaction calls at a time.
    const targetHashBuff = createHash('sha256').update('global:deposit_finalize').digest().slice(0, 8)
    async function performGetTransactions (txSignatures) {
      await Promise.all(txSignatures.map(async (txSignature) => {
        let tx = await connection.getTransaction(
          txSignature.signature,
          { commitment: 'confirmed' }
        )
        while (tx === null) {
          tx = await connection.getTransaction(
            txSignature.signature,
            { commitment: 'confirmed' }
          )
        }
        await Promise.all(tx.transaction.message.instructions.map(async (ix) => {
          const ixDataDecoded = decode58(ix.data)
          // If the transaction was not successful deposit_finalize, return.
          if (!ixDataDecoded.slice(0, 8).equals(targetHashBuff) || tx.meta.err !== null) {
            return
          }
          let logs = tx.meta.logMessages
          // The newer version of Anchor also logs the instruction type. Strip this
          // to be compatible with the old Anchor version.
          logs = logs.filter(log => log !== 'Program log: Instruction: DepositFinalize')

          // Strip all logs before `Program ... invoke` in order to protect against wrapper attacks.
          let idx = 0
          for (const log of logs) {
            if (log.startsWith(`Program ${OTTER_PROGRAM_ID.toString()} invoke`)) {
              break
            }
            idx += 1
          }
          logs = logs.slice(idx)

          if (logs.indexOf(`Program ${OTTER_PROGRAM_ID.toString()} success`) === -1) {
            throw Error('Invalid Logs.')
          }

          const merkleStateIndex = parseInt(logs[1].split(' = ')[1])
          const merkleStateHashPath = JSON.parse(logs[2].split(' = ')[1])
          const merkleStateRoot = JSON.parse(logs[3].split(' = ')[1])
          allPreviousDeposits[merkleStateIndex] = {
            hashPath: merkleStateHashPath,
            root: merkleStateRoot
          }
        }))
      }))
    }
    for (let i = 0; i < Math.ceil(allTxSignatures.length / 500); i++) {
      await performGetTransactions(allTxSignatures.slice(500 * i, 500 * (i + 1)))
    }
  }

  const minimumMerkleStateIndex = Math.min(...Object.keys(allPreviousDeposits))
  const maximumMerkleStateIndex = Math.max(...Object.keys(allPreviousDeposits))

  callback('Generating zero-knowledge proof')

  // Compute the pathIndices, and create mergedPathElements from allPreviousDeposits.
  const pathIndices = []
  const mergedPathElements = []
  for (let i = 0; i < LEVELS; i++) {
    const depositIndexBit = minimumMerkleStateIndex >> i & 1
    pathIndices.push(depositIndexBit)
    if (depositIndexBit === 0) {
      const firstPotentialAffector = minimumMerkleStateIndex - (minimumMerkleStateIndex % Math.pow(2, i)) + Math.pow(2, i)
      const lastPotentialAffector = firstPotentialAffector + Math.pow(2, i) - 1
      if (firstPotentialAffector > maximumMerkleStateIndex) {
        mergedPathElements.push(ZEROS[i])
      } else {
        const affector = Math.min(maximumMerkleStateIndex, lastPotentialAffector)
        mergedPathElements.push(
          bigInt.beBuff2int(allPreviousDeposits[affector].hashPath[i])
        )
      }
    } else {
      mergedPathElements.push(depositFilledSubtrees[i])
    }
  }

  // Generate the ZK proof.
  const snarkInput = {
    // Public Inputs
    root: bigInt.beBuff2int(allPreviousDeposits[maximumMerkleStateIndex].root),
    nullifierHash: nullifierHash,
    recipient: bigInt.beBuff2int(recipient.encode()),
    relayer: bigInt.beBuff2int(relayer.encode()),
    fee: fee,
    // `refund` is specific to Ethereum's implementation of token transfers
    // (destination addresses can reject inbound token transfers), so we ignore
    // it. It is an isolated circuit input.
    refund: 0,
    // Private Inputs
    nullifier: nullifier,
    secret: secret,
    pathElements: mergedPathElements,
    pathIndices: pathIndices
  }
  const groth16 = await buildGroth16()
  const proofData = await genWitnessAndProve(
    groth16,
    snarkInput,
    circuit,
    provingKey
  )
  const proof = toSolidityInput(proofData)
  return proof
}

export async function withdraw (
  note: string,
  recipient: string,
  relayer: string,
  fee: number,
  relayerBaseUrl: string,
  callback: () => void,
  toast: () => void
) {
  await errorToast(toast, 'Failed to load circuit', async () => {
    if (circuit === null) {
      circuit = await circuitPromise
    }
  })
  await errorToast(toast, 'Failed to load proving key', async () => {
    if (provingKey === null) {
      provingKey = await provingKeyPromise
    }
  })
  let proof
  await errorToast(toast, 'Failed to generate the proof', async () => {
    proof = await generateProof(
      note,
      new web3.PublicKey(recipient),
      new web3.PublicKey(relayer),
      fee,
      callback
    )
  })

  callback('Sending proof to relayer')

  // Send the proof and publicSignals to the relayer.
  let relayerInitResp
  await errorToast(toast, 'Invalid response from relayer', async () => {
    relayerInitResp = await fetch(
      `${relayerBaseUrl}/relay`,
      {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(proof)
      }
    ).then(resp => resp.json())
    if (!relayerInitResp.ok) {
      throw new Error('Response from /relay was not a 2XX.')
    }
  })
  await sleep(10000)

  const startTime = Date.now()
  while (true) {
    await sleep(2500)
    const withdrawStateAccountInfo = await program.account.withdrawState.getAccountInfo(
      new web3.PublicKey(relayerInitResp.withdrawState)
    )

    // If the WithdrawState is null, then either the `withdraw_init`
    // transaction has not yet been confirmed by the cluster, or the
    // `withdraw_finalize` has been processed and the account lamports have
    // been recovered. As a heuristic, we do not consider the withdraw final
    // until at least 60 seconds have passed.
    if (withdrawStateAccountInfo === null) {
      if (Date.now() - startTime > 60000) {
        return
      } else {
        continue
      }
    }

    // Manually deserialize the phases.
    const withdrawStateData = withdrawStateAccountInfo.data
    const phaseGlobVkxOrPairing = withdrawStateData.readUInt8(392 + 64)
    const phaseVkxIter = withdrawStateData.readUInt8(392 + 65)
    const phaseVkxMulIter = withdrawStateData.readUInt16LE(392 + 66)
    const phasePairingIter = withdrawStateData.readUInt8(392 + 68)
    const phasePairingIterStep = withdrawStateData.readUInt16LE(392 + 70)

    // linearPhase is between 0 and 20086, inclusive.
    let linearPhase = 0
    switch (phaseGlobVkxOrPairing) {
      case 0:
        linearPhase = 257 * phaseVkxIter + phaseVkxMulIter
        break
      case 1:
        linearPhase = 257 * 6 + 4635 * phasePairingIter + phasePairingIterStep
        break
      case 2:
        linearPhase = 257 * 6 + 4636 * 4
        break
      default:
        throw new Error('Unreachable.')
    }

    let callbackText = ''
    if (linearPhase === 20086) {
      callbackText = 'Finalizing the withdraw'
    } else {
      callbackText = `Confirmed ${(100 * linearPhase / 20086).toFixed(0)}% of the transactions`
    }
    callback(callbackText)
  }
}
