import { web3 } from '@project-serum/anchor'

import { randomBytes } from 'crypto'
import { babyJub, pedersenHash } from 'circomlib'

import {
  LEVELS,
  OTTER_PROGRAM_ID,
  errorToast,
  program,
  sleep
} from './ContractCommon'

const ROUNDS_PER_IX_FEISTEL = 11
// Note that IXS_PER_TX_DEPOSIT = 79 is the theoretical maximum due to tx data
// size limits, and that IXS_PER_DEPOSIT = 55 is the apparent upper-bound due
// to per-account Compute Unit limits (per the relayer). However, the UI is
// more smooth and tx processing is more reliable if we use a smaller tx size.
const IXS_PER_TX_DEPOSIT = 7
const NUM_ADVANCES_DEPOSIT = LEVELS * 2 * (Math.ceil(220 / ROUNDS_PER_IX_FEISTEL) + 1)

async function depositInit (
  merkleState,
  merkleStateBump,
  callback,
  toast
) {
  const preimage = randomBytes(62)
  if (preimage.toString('hex').length !== 124) {
    throw Error('Invalid generated preimage.')
  }
  const commitment = babyJub.unpackPoint(
    pedersenHash.hash(preimage)
  )[0].beInt2Buff(32)

  const depositState = web3.Keypair.generate()
  const depositInitTx = new web3.Transaction()
  depositInitTx.add(program.instruction.depositInit(
    merkleStateBump,
    commitment,
    {
      accounts: {
        depositState: depositState.publicKey,
        merkleState: merkleState,
        user: program.provider.wallet.publicKey,
        systemProgram: web3.SystemProgram.programId
      },
      signers: [depositState]
    }
  ))
  // Adapted from anchor.program.provider.send.
  depositInitTx.feePayer = program.provider.wallet.publicKey
  depositInitTx.recentBlockhash = (
    await program.provider.connection.getRecentBlockhash()
  ).blockhash

  let signedTx
  await errorToast(toast, 'Signature was rejected', async () => {
    signedTx = await program.provider.wallet.signTransaction(depositInitTx)
  })
  signedTx.partialSign(depositState)
  callback('Sending transaction 1 of 3')

  const depositInitTxSignature = await program.provider.connection.sendRawTransaction(
    signedTx.serialize(),
    { skipPreflight: false, maxRetries: 1024 }
  )

  callback('Confirming transaction 1 of 3')
  await errorToast(toast, 'Failed to confirm transaction 1 of 3', async () => {
    await program.provider.connection.confirmTransaction(
      depositInitTxSignature,
      'confirmed'
    )
  })

  // If we don't sleep here, the fetch of depositState sometimes fails.
  await sleep(15000)

  let depositStateBefore
  await errorToast(toast, 'Failed to fetch depositState', async () => {
    depositStateBefore = await program.account.depositState.fetch(depositState.publicKey)
  })
  const depositInitInfo = {
    preimage: preimage,
    startingFilledSubtrees: depositStateBefore.newFilledSubtrees
  }
  return [depositState, depositInitInfo]
}

async function allDepositAdvance (depositState, callback, toast) {
  const depositAdvanceIxs = []
  for (let i = 0; i < NUM_ADVANCES_DEPOSIT; i++) {
    const ix = program.instruction.depositAdvance(
      Math.floor(i / IXS_PER_TX_DEPOSIT),
      {
        accounts: {
          depositState: depositState.publicKey
        }
      }
    )
    depositAdvanceIxs.push(ix)
  }

  const depositAdvanceTxs = []
  for (let i = 0; i < Math.ceil(NUM_ADVANCES_DEPOSIT / IXS_PER_TX_DEPOSIT); i++) {
    const tx = new web3.Transaction()
    for (const ix of depositAdvanceIxs.slice(
      IXS_PER_TX_DEPOSIT * i, IXS_PER_TX_DEPOSIT * (i + 1)
    )) {
      tx.add(ix)
    }
    depositAdvanceTxs.push({ tx: tx, signers: [] })
  }

  let totalConfirmations = 0
  async function signSendAndConfirmDepositSubset (depositAdvanceTxsSubset, isFirstCall) {
    if (isFirstCall) {
      callback('Signing transaction 2 of 3')
    }
    // Adapted from anchor.program.provider.sendAll.
    const blockhash = await program.provider.connection.getRecentBlockhash()
    const txs = depositAdvanceTxsSubset.map((r) => {
      const tx = r.tx
      let signers = r.signers
      if (signers === undefined) {
        signers = []
      }
      tx.feePayer = program.provider.wallet.publicKey
      tx.recentBlockhash = blockhash.blockhash
      signers
        .filter((s): s is web3.Signer => s !== undefined)
        .forEach((kp) => {
          tx.partialSign(kp)
        })
      return tx
    })

    let signedTxs
    await errorToast(toast, 'Signature was rejected', async () => {
      signedTxs = await program.provider.wallet.signAllTransactions(txs)
    })
    if (isFirstCall) {
      callback('Sending transaction 2 of 3')
    }

    const depositAdvanceTxSignatures = await Promise.all(signedTxs.map(async tx => {
      const rawTx = tx.serialize()
      return await program.provider.connection.sendRawTransaction(
        rawTx,
        { skipPreflight: true, maxRetries: 1024 }
      )
    }))
    if (isFirstCall) {
      callback('Confirming transaction 2 of 3')
    }
    const confirmations = await Promise.all(depositAdvanceTxSignatures.map(async (txSignature, i) => {
      let isConfirmed = false
      let isTimeout = false;
      (async () => {
        while (!isConfirmed && !isTimeout) {
          const rawTx = signedTxs[i].serialize()
          await program.provider.connection.sendRawTransaction(
            rawTx,
            { skipPreflight: true, maxRetries: 16 }
          )
          await sleep(300)
        }
      })()
      try {
        await program.provider.connection.confirmTransaction(txSignature, 'confirmed')
      } catch (err) {
        isTimeout = true
        return false
      }
      isConfirmed = true
      totalConfirmations += 1
      callback(
        'Confirming transaction 2 of 3',
        `${(100 * totalConfirmations / depositAdvanceTxs.length).toFixed(0)}% confirmed`
      )
      return true
    }))
    const failedDepositAdvanceTxsSubset = depositAdvanceTxsSubset.filter(
      (tx, i) => !confirmations[i]
    )
    if (failedDepositAdvanceTxsSubset.length > 0) {
      await signSendAndConfirmDepositSubset(failedDepositAdvanceTxsSubset, false)
    }
  }

  await signSendAndConfirmDepositSubset(depositAdvanceTxs, true)
}

async function depositFinalize (
  merkleState,
  merkleStateBump,
  depositState,
  depositInitInfo,
  callback,
  toast
) {
  callback('Signing transaction 3 of 3')
  const depositFinalizeTx = new web3.Transaction()
  depositFinalizeTx.add(program.instruction.depositFinalize(
    merkleStateBump,
    {
      accounts: {
        depositState: depositState.publicKey,
        merkleState: merkleState,
        user: program.provider.wallet.publicKey,
        systemProgram: web3.SystemProgram.programId
      }
    }
  ))
  // Adapted from anchor.program.provider.send.
  depositFinalizeTx.feePayer = program.provider.wallet.publicKey
  depositFinalizeTx.recentBlockhash = (
    await program.provider.connection.getRecentBlockhash()
  ).blockhash

  let signedTx
  await errorToast(toast, 'Signature was rejected', async () => {
    signedTx = await program.provider.wallet.signTransaction(depositFinalizeTx)
  })
  callback('Sending transaction 3 of 3')

  let depositFinalizeTxSignature
  await errorToast(toast, 'Error during finalization', async () => {
    depositFinalizeTxSignature = await program.provider.connection.sendRawTransaction(
      signedTx.serialize(),
      { skipPreflight: true, maxRetries: 1024 }
    )
  })

  let pathElements = Buffer.alloc(0)
  for (const filledSubtree of depositInitInfo.startingFilledSubtrees) {
    pathElements = Buffer.concat([pathElements, Buffer.from(filledSubtree)])
  }
  const note = `otter-${depositFinalizeTxSignature}-${depositInitInfo.preimage.toString('hex')}-${pathElements.toString('hex')}-${program.programId}`
  console.log('Note:', note)

  callback('Confirming transaction 3 of 3')
  await errorToast(toast, 'Failed to confirm transaction 3 of 3', async () => {
    await program.provider.connection.confirmTransaction(
      depositFinalizeTxSignature,
      'confirmed'
    )
  })

  return note
}

export async function deposit (
  callback: () => void,
  toast: () => void
): string {
  if (program === null) {
    throw new Error('program is null')
  }
  const [merkleState, merkleStateBump] = await web3.PublicKey.findProgramAddress(
    [Buffer.from('merkle')],
    OTTER_PROGRAM_ID
  )

  // If the merkleState has no data, run initialization.
  if (await program.provider.connection.getAccountInfo(merkleState) === null) {
    const initializeTxSignature = await program.rpc.initialize(
      merkleStateBump,
      {
        accounts: {
          merkleState: merkleState,
          user: program.provider.wallet.publicKey,
          systemProgram: web3.SystemProgram.programId
        },
        signers: []
      }
    )
    await program.provider.connection.confirmTransaction(initializeTxSignature, 'finalized')
  }

  // Run init, advance, and finalize.
  const [depositState, depositInitInfo] = await depositInit(
    merkleState,
    merkleStateBump,
    callback,
    toast
  )
  await allDepositAdvance(depositState, callback, toast)
  const note = await depositFinalize(
    merkleState,
    merkleStateBump,
    depositState,
    depositInitInfo,
    callback,
    toast
  )

  return note
}
