import React from 'react'

import {
  Box,
  Center,
  FormControl,
  FormErrorMessage,
  Input,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Spinner,
  Text
} from '@chakra-ui/react'

import { web3 } from '@project-serum/anchor'
import {
  getHashedName,
  getNameAccountKey,
  NameRegistryState
} from '@bonfida/spl-name-service'

import { DepositWithdrawButton } from './ApplicationCommon'
import { connection } from './ContractCommon'

import otterLogoGlyph from './icons/otterlogo_glyph.svg'

const SOL_TLD_AUTHORITY = new web3.PublicKey('58PwtjSDuFHuUkYjH9BYnnQKHfwo9reZhC2zMJv9JPkx')
export const RELAYER = 'otter-relayer'
console.log('Relayer:', RELAYER + '.sol')

interface WithdrawModalProps {
  isOpen: boolean
  onOpen: () => void
  onClose: () => void
  withdrawRelayerFee: number
  withdrawRelayerAddress: string
  withdrawRelayerUrlBase: string
  withdrawInputNote: string
  withdrawInputRecipient: string
  withdrawModalStageZeroLoadingText: string
  withdrawModalStage: string
  withdrawSetState: () => void
  withdrawResetState: () => void
}
export function WithdrawModal (props: WithdrawModalProps) {
  function stageZeroContent () {
    return (
      <>
        <ModalHeader>
          {`Withdrawing ${1 - props.withdrawRelayerFee / web3.LAMPORTS_PER_SOL} SOL`}
        </ModalHeader>
        <ModalBody>
          <Spinner
            color="bistre"
            size="xl"
          />
          <Text
            mt="5%"
          >
            {props.withdrawModalStageZeroLoadingText}
          </Text>
        </ModalBody>
      </>
    )
  }

  function stageOneContent () {
    return (
      <>
        <ModalHeader>
          {`Withdrew ${1 - props.withdrawRelayerFee / web3.LAMPORTS_PER_SOL} SOL!`}
        </ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <Center mt="5%">
            <img src={otterLogoGlyph} />
          </Center>
          <Text mt="5%">
            {`${1 - props.withdrawRelayerFee / web3.LAMPORTS_PER_SOL} SOL has been credited to account`}
          </Text>
          <Text mt="0%" fontFamily="Courier">
            {props.withdrawInputRecipient}
          </Text>
        </ModalBody>
      </>
    )
  }

  return (
    <Modal
      isOpen={props.isOpen}
      onClose={() => {
        props.withdrawResetState()
        props.onClose()
      }}
      closeOnOverlayClick={props.withdrawModalStage === 1}
      closeOnEsc={props.withdrawModalStage === 1}
      isCentered
      size="lg"
    >
      <ModalOverlay />
      <ModalContent
        bg="champagne"
        p="1% 1% 2% 1%"
        borderRadius="10px"
      >
        {
          {
            0: stageZeroContent(),
            1: stageOneContent()
          }[props.withdrawModalStage]
        }
      </ModalContent>
    </Modal>
  )
}

interface WithdrawProps {
  onOpenModal: () => void
  withdrawRelayerAddress: string
  withdrawRelayerFee: number
  withdrawInputNote: string
  withdrawInputNoteIsSpent: boolean
  withdrawInputNoteNeverDeposited: boolean
  withdrawInputRecipient: string
  withdrawSetState: () => void
  performWithdraw: () => void
}
export class Withdraw extends React.Component<WithdrawProps> {
  constructor (props: WithdrawProps) {
    super(props)
    this.state = {
      isManuallyPaused: false
    }
  }

  async componentDidMount () {
    // fetch('https://proxy.otter.cash/isManuallyPaused')
    //   .then(resp => resp.json())
    //   .then(resp => this.setState({ isManuallyPaused: resp.isPaused }))

    // Fetch relayer URL from Bonfida domain.
    const relayerHashedName = await getHashedName(RELAYER)
    const relayerDomainKey = await getNameAccountKey(
      relayerHashedName,
      undefined,
      SOL_TLD_AUTHORITY
    )
    const relayerRegistry = await NameRegistryState.retrieve(connection, relayerDomainKey)
    const relayerUrlBase = 'https://' + relayerRegistry.registry.data.toString().replace(/\0/g, '')

    // Query the relayerFee from the relayerUrl.
    const relayerFeeAndAddress = await fetch(
      relayerUrlBase + '/feeAndAddress'
    ).then(resp => resp.json())
    this.props.withdrawSetState({
      withdrawRelayerFee: relayerFeeAndAddress.fee,
      withdrawRelayerAddress: relayerFeeAndAddress.address,
      withdrawRelayerUrlBase: relayerUrlBase
    })
  }

  render () {
    const inputNoteIsErrorSyntax = this.props.withdrawInputNote.length > 0 && (
      this.props.withdrawInputNote.split('-').length !== 5 ||
      this.props.withdrawInputNote.includes(' ')
    )
    const inputRecipientIsError = this.props.withdrawInputRecipient.length > 0 &&
      this.props.withdrawInputRecipient.includes(' ')

    const buttonDisabled = (
      inputNoteIsErrorSyntax ||
      this.props.withdrawInputNoteIsSpent ||
      this.props.withdrawInputNoteNeverDeposited ||
      inputRecipientIsError ||
      this.props.withdrawInputNote.length === 0 ||
      this.props.withdrawInputRecipient.length === 0
    )
    let errorMessage = ''
    if (inputNoteIsErrorSyntax) {
      errorMessage = 'Invalid note.'
    } else if (this.props.withdrawInputNoteIsSpent) {
      errorMessage = 'Note has already been spent.'
    } else if (this.props.withdrawInputNoteNeverDeposited) {
      errorMessage = 'Note was never deposited.'
    }
    return (
      <Box m="6% 8% 6% 8%">
        <Text
          fontSize={['0.9em', '1.2em']}
          textAlign="left"
        >
          Secret Note
        </Text>
        <FormControl
          isInvalid={
            inputNoteIsErrorSyntax ||
              this.props.withdrawInputNoteIsSpent ||
              this.props.withdrawInputNoteNeverDeposited
          }
        >
          <Input
            value={this.props.withdrawInputNote}
            onChange={(e) => {
              this.props.withdrawSetState({ withdrawInputNote: e.target.value })
            }}
            placeholder="Paste your secret note"
            borderColor="bistre"
            focusBorderColor="bistre"
            fontSize={['0.8em', '1.0em']}
            fontFamily="Courier"
            color="bistre"
            opacity="70%"
            _hover={{ borderColor: 'bistre' }}
            _placeholder={{ color: 'bistre' }}
          />
          <FormErrorMessage>
            {errorMessage}
          </FormErrorMessage>
        </FormControl>
        <Text
          mt="5%"
          fontSize={['0.9em', '1.2em']}
          textAlign="left"
        >
          Withdraw Address
        </Text>
        <FormControl
          isInvalid={inputRecipientIsError}
        >
          <Input
            value={this.props.withdrawInputRecipient}
            onChange={(e) => {
              this.props.withdrawSetState({ withdrawInputRecipient: e.target.value })
            }}
            mt="1%"
            placeholder="Address to receive the funds"
            borderColor="bistre"
            focusBorderColor="bistre"
            fontSize={['0.8em', '1.0em']}
            fontFamily="Courier"
            color="bistre"
            opacity="70%"
            _hover={{ borderColor: 'bistre' }}
            _placeholder={{ color: 'bistre' }}
          />
          <FormErrorMessage>
            Invalid address.
          </FormErrorMessage>
        </FormControl>
        <DepositWithdrawButton
          isDeposit={false}
          isManuallyPaused={this.state.isManuallyPaused}
          disableClick={buttonDisabled}
          onClick={async () => {
            // If we have not yet received a response from the relayer, ignore this click.
            if (this.props.withdrawRelayerAddress === '' || this.props.withdrawRelayerFee === null) {
              return
            }
            this.props.onOpenModal()
            await this.props.performWithdraw()
          }}
          text={
            this.props.withdrawRelayerFee === null
              ? 'Withdraw SOL'
              : `Withdraw ${1 - this.props.withdrawRelayerFee / web3.LAMPORTS_PER_SOL} SOL`
          }
          style={{
            w: ['90%', '65%'],
            mt: '8%',
            fontSize: ['0.9em', '1.1em']
          }}
        />
        <Text
          mt="2%"
          fontSize={['0.6em', '0.8em']}
          opacity="70%"
        >
          {
            this.props.withdrawRelayerFee === null
              ? null
              : `${RELAYER}.sol charges a ${this.props.withdrawRelayerFee / web3.LAMPORTS_PER_SOL} SOL relay fee`
          }
        </Text>
      </Box>
    )
  }
}
